<template>
    <div class="sidebar">
        <GeminiScrollbar class="sidebar-scroll" :style="{'height':sidebarHeight}">
            <SideList :list='sideListAll'></SideList>
        </GeminiScrollbar>
    </div>
</template>

<script>
    import Vue from 'vue'
    import SideList from './child/sidebar/sideList.vue'

    export default {
        name:'SideBar',
        components:{
            SideList,
        },
        data(){
            return{
                sideListAll:[
                    {
                        title:'',
                        modifiable:false,
                        retractable:false,
                        extended:true,
                        list:[
                            {icon:'musicbyinle',word:'',url:'/index',onClick:true},
                            {icon:'radiowaves',word:'FM',url:'/private',onClick:false},
                            {icon:'ico-presetvideo-IJuv',word:'MV',url:'/mv',onClick:false},
                            {icon:'icon_friend',word:'',url:'/friend',onClick:false}
                        ]
                    },
                    {
                        title:'',
                        modifiable:false,
                        retractable:false,
                        extended:true,
                        list:[
                            {icon:'musicnot',word:'',url:'/localmusic', onClick:false},
                            {icon:'download',word:'',url:'/download', onClick:false},
                            {icon:'cloud',word:'',url:'/musiccloud',onClick:false},
                            {icon:'follower',word:'',url:'/follow',onClick:false}
                        ]
                    },
                    {
                        title:'',
                        modifiable:true,
                        retractable:true,
                        extended:true,
                        list:[                       
                            {icon:'heart1',word:'',url:'/favouritelist', onClick:false},
                            {albumID:3,icon:'musicmenu',word:'1',url:'/musiclist1', onClick:false},
                            {albumID:2,icon:'musicmenu',word:'2',url:'/musiclist2',onClick:false},
                            {albumID:1,icon:'musicmenu',word:'3',url:'/misiclist3',onClick:false}
                        ]
                    }
                ],
                sidebarHeight:null
            }
        },
        mounted() {            
            // Virtual DOM
            this.sidebarHeight = document.documentElement.clientHeight-100+'px';
            // windowresize
            const app = this;
            window.onresize = function temp() {
                app.sidebarHeight = document.documentElement.clientHeight-100+'px';
            };
        },
    }
</script>

<style scoped>
    .sidebar{
        background: #f5f5f7;
        width: 200px;
        position: absolute;
        left: 0;
        top:50px;
        border-right: 1px solid #eee;
    }
    .gm-scrollbar.-vertical {
        background-color: #f0f0f0
    }
    
    /* horizontal scrollbar track */
    .gm-scrollbar.-horizontal {
        background-color: transparent;
    }
    
    /* scrollbar thumb */
    .gm-scrollbar .thumb {
        background-color: rebeccapurple;
    }
    .gm-scrollbar .thumb:hover {
        background-color: fuchsia;
    }
</style>





